html {
	scroll-behavior: smooth;
	box-sizing: border-box;
}

*,
*::before,
*::after {
	box-sizing: inherit;
}

body {
	font-family: var(--font-family);
	margin: 0;
	font-weight: 400;
	line-height: 1.2;
}

h1,
h2,
h3,
h4,
h5,
h6 {
	margin: 0;
	padding: 0;
}

p {
	margin: 0;
	padding: 0;
}

picture {
	display: block;
}

img {
	display: block;
	height: auto;
	max-width: 100%;
	object-fit: cover;
}

a {
	display: inline-block;
	color: inherit;
	text-decoration: none;
}

ul,
ol {
	margin: 0;
	padding: 0;
	list-style: none;
}

a,
button,
input,
select,
textarea {
	outline: 0 !important;
}

button {
	display: inline-block;
	font-family: inherit;
	font-size: inherit;
	font-weight: inherit;
	line-height: normal;
	text-align: center;
	border: 0;
	cursor: pointer;
}

